
var kaObj = JSON.parse(sessionStorage.kaObj)
var userName = localStorage.userName
var passWord = localStorage.passWord

import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import {mapMutations} from 'vuex'
import MeScroll from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'
export default {
name:'searc',
components:{
Group,
Datetime,
Selector,
Search,
Divider,
XButton,
Flexbox,
FlexboxItem,
XTable,
XInput
},
data(){
return{
startDate:'',
endDate:'',
list:[
{key:'1',value:'自由'},
{key:'2',value:'已生成订单'},
],
defaultValue:'',
mescroll: null, //mescroll实例对象
dataList:[], //列表数据
isShow:false,
Tday:'',
Numbervalue:''
}
},
methods:{
...mapMutations(['getOrderId','getOrderList']),
tj(){               //查询方法
if(this.startDate || this.endDate || this.Numbervalue || this.defaultValue){      //查询条件不能为空
if(!this.isShow && this.mescroll){   //当查询条件变化时
this.isShow = true

this.mescroll.resetUpScroll();
}else if(this.mescroll == null){
this.isShow = true
this.mescroll = new MeScroll("mescroll", { //初始化mescroll,确保此处配置的id能够被找到
up: {

callback: this.upCallback,

empty:{
warpId : 'mescroll' ,
icon : null ,
tip : "暂无订单数据,请重新输入查询条件~" ,
btntext : "" ,
btnClick : null,
supportTap : false
},

noMoreSize:1,


hardwareClass:"mescroll-hardware",


htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>',


htmlNodata:'<p class="upwarp-nodata"> -- 加载完毕 -- </p>',


auto:true,
},
down: {
use:false
}

});
}
}else{
this.$vux.alert.show({
title: '注意',
content: '查询条件不能为空',
})
}

},
upCallback(page) {

this.$axios.get(this.url+'preorderKaController.do?findKaddcxList', {
params: {
page: page.num, //页码
rows: page.size, //每页长度
userName:userName,
passWord:passWord,
customerCode:kaObj.customerCode,



orderNumber:this.Numbervalue,
orderDateStart:this.startDate,
orderDateEnd:this.endDate,
state:this.defaultValue
}
}).then((response)=> {

let arr = response.data.orders;

if (page.num == 1) this.dataList = [];

this.dataList = this.dataList.concat(arr);

this.$nextTick(() => {
this.mescroll.endSuccess(arr.length);
})

}).catch((e)=> {

this.mescroll.endErr();
})

},

detail(id){

this.$axios.get(this.url+'preorderKaController.do?findKaddcxDetail',{
params:{
userName:userName,
passWord:passWord,


orderId:id
}
}).then(res=>{
console.log(res)
let obj =res.data.order
let arr = []
arr.push(obj)
arr.push(res.data.orderProducts)
console.log(arr)
this.getOrderList(arr)
this.$router.push({path:"/detail"})
}).catch(e=>{
this.$vux.alert.show({
title: '注意',
content: '服务器出错,请稍后再试',
})
})
}

},
watch:{
Numbervalue(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
},
defaultValue(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
},
startDate(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
},
endDate(val,oldValue){
if(val != oldValue){
this.isShow = false;
}
}
},
created(){
let dayN = new Date()  
let year = dayN.getFullYear()
let mon = dayN.getMonth()
if(mon+1 < 10){
mon = '0' + (mon+1)
}else{
mon = (mon+1)
}
let day = dayN.getDate()
if(day < 10){
day = '0' + day
}
let Tday = year + '-' + mon + '-' + day
this.Tday = Tday
this.startDate = Tday
this.endDate = Tday
},
beforeRouteLeave (to, from, next) {  //离开页面前,进入到首页的时候搜索条件初始化
if(to.path == '/'){
this.isShow = false;
this.mescroll == null;
}
next()
}

}




